<template>
	<div>
		<div class="back-title"><span>账号信息</span></div>
		<div class="pagebox">
			<Menu mode="horizontal" active-name="1" @on-select="turnUrl" class="item-title-box">
		        <MenuItem name="/user/info">
		            	详细信息
		        </MenuItem>
		        <MenuItem name="/user/info/setting">
		            	登录设置
		        </MenuItem>
		    </Menu>
		    <router-view></router-view>
			
		</div>
	</div>
</template>

<script>
 export default {
   methods: {
     turnUrl (name) {
       this.$router.push(name)
     }
   }
 }
</script>


